                      </div>
                    </div>
                  </div>
                </div>
                <button class="store_product_modal_btn_buy" @click="buyGood(currentItem.id)">{{ $t('Add_to_Basket') }}</button>
                <button class="store_product_modal_btn_buy_now" @click="buyGood(currentItem.id,true)">{{ $t('Buy_now') }}</button>
